@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
  text-align: center;
}

.navbar {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  font-size: 25px;
  position: relative;
}

.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  font-size: 20px;
  font-weight: bold;
}
.navbar a:hover {
  color: blue;
}
.content {
  max-width: 700px;
  margin: 40px auto;
  padding: 20px;
}

.auth {
  display: none;
  position: absolute;
  margin-left: calc(100% - 66%);
}

.auth li {
  list-style: none;
  margin-top: 30px;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
}

.profile:hover .auth {
  display: none;
}

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}

.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.blog-preview a {
  text-decoration: none;
}

.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
  font-size: 20px;
}
.create h2 {
  font-size: 25px;
  margin-bottom: 30px;
}
.create input,
.create textarea,
.create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
  font-size: 20px;
}
.create button {
  margin-top: 20px;
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 15px;
  cursor: pointer;
  font-size: 18px;
}

.blog-details h2 {
  margin-bottom: 30px;
}

.blog-details div {
  margin: 30px 0;
  font-size: 20px;
}

.blog-details p {
  font-size: 20px;
  color: #f1356d;
}

.blog-details button {
  margin-top: 30px;
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 15px;
  cursor: pointer;
  font-size: 18px;
}

.about h3 {
  padding: 8px;
}

.about a {
  padding: 10px;
  font-size: 20px;
}

.todo-list {
  border: 1px solid black;
  border-radius: 8px;
  margin: auto;
  text-align: center;
}

.todo-list h2 {
  padding: 30px;
  margin-bottom: 30px;
  font-size: 25px;
}

.todo-list p {
  padding: 0 0 25px;
  font-size: 20px;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  text-align: start;
  margin-left: -60px;
}

.todo-list input {
  padding: 10px;
  border: hidden;
  font-size: 20px;
  border-style: none;
  width: 60%;
}
.todo-list button {
  margin-top: 10px;
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 15px;
  cursor: pointer;
  font-size: 18px;
  margin-left: 20px;
  margin-bottom: 50px;
}

.todo-preview {
  display: -webkit-flex;
  display: flex;
  margin-right: 20px;
}

.todo-preview button {
  display: -webkit-flex;
  display: flex;
  margin-right: 20px;
}

.checkbox {
  margin-top: 6px;
  margin-left: -50px;
}

.canvas {
  background-color: #272727;
  height: 700px !important;
  position: absolute;
  left: 0;
  top: 0;
  margin-right: 1000px;
}

.notfound h2 {
  margin-bottom: 30px;
}

.notfound a {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 15px;
  cursor: pointer;
  font-size: 18px;
  text-decoration: none;
}

